	module.exports = {"data":{"markdownRemark":{"html":"<p>It’s that time again: the time where I exalt in my ability to do something that, at the beginning of this year would have been all but impossible. It took two days (and a bit more than that previously) to solve, but I was able to use functional programming and recursion to solve my toughest FreeCodeCamp bonfire yet (until the next one) Exact Change.</p>\n<h2>The Bonfire</h2>\n<p>The function of this bonfire was to:</p>\n<blockquote>\n<p>Design a cash register drawer function that accepts purchase price as the first argument, payment as the second argument, and cash-in-drawer (cid) as the third argument. cid is a 2d array listing available currency. Return the string “Insufficient Funds” if cash-in-drawer is less than the change due. Return the string “Closed” if cash-in-drawer is equal to the change due. Otherwise, return change in coin and bills, sorted in highest to lowest order.</p>\n<p>– FreeCodeCamp</p>\n</blockquote>\n<h2>Sticking Points</h2>\n<h3>Thinking Clearly</h3>\n<p>The biggest sticking point overall was being able to think clearly about the actual problem. I’ve been trying to practice the 4Ps of Programming championed by Andrew Chalkley over at Treehouse: Prepare, Plan, Perform, and Perfect. It’s a great way to tackle any project and it was really a big help here… when I got to the point where I could actually plan my approach. I originally gave up on this bonfire after several unsuccessful days, but coming back to it several weeks later and a few more Treehouse lessons down, I found that I was able to finally start talking about the issue in plain language. My planning looked like this:</p>\n<pre><code class=\"language-javascript\">// find change owed\n\n//if changeOwed > totalCash\n//return the string \"Insufficient Funds\"\n\n//else if changeOwed === totalCash\n//return the string \"Closed\"\n\n//Else return array of change denominations, starting from the highest possible denom to the lowest\n//implies recursion?\n</code></pre>\n<p>I figured it was important to get in a win early on, so I started strong by programming this simple if/else if/else block:</p>\n<pre><code class=\"language-javascript\">var changeOwed = cash\"price;\",\nvar totalCash = [];\n  totalCash = totalCash.concat.apply(totalCash, cid); //projects and flattens cid array\n  totalCash = totalCash.filter(function(cashItem){\n    if (typeof cashItem === \"number\"){\n      return cashItem;\n    }\n  }).reduce(function(prev, next){\n    return prev + next;\n  });\n//if changeOwed > totalCash\n    //return the string \"Insufficient Funds\"\n   if (changeOwed > totalCash){\n     change = \"Insufficient Funds\";\n    } else if (changeOwed === totalCash){\n      //else if changeOwed === totalCash\n    //return the string \"Closed\"\n      change = \"Closed\";\n    } else {\n      //Else return array of change denominations, starting from the highest possible denom to the lowest\n      //implies recursion?\n\n    }\n  return change;\n</code></pre>\n<p>That took care of two important test cases but still left me unable to wrap my head around how to tackle the hard bit: pulling cash out of the drawer.</p>\n<h3>Why oh Why did They Give me an Array?</h3>\n<p>The next sticking point was undoubtedly, wishing that the drawer had been an array of objects. Perhaps especially since the given functional programming practice deals largely with arrays of objects, it was hard to wrap my head around how to access what I needed in a 2d array. At first, I did try to map the arrays to objects, but I kept hitting a wall because: a. that wasn’t actually helping me get any closer to understanding how to approach the problem and b. the final result still had to be a 2d array and I didn’t buy that converting to and from an object would be good practice. So, back to the drawing board. How did actual cash registers work?</p>\n<h2>A Functional Solution</h2>\n<p>I became convinced of the need for a function that could help me solve the issue. So I created one that housed a series of if/else statements that I could run the changeOwed through repeatedly (that is, a recursive solution):</p>\n<pre><code class=\"language-javascript\">//To give change\n    //compare the amount owed\n    //subtract the largest amount possible from the largest denom possible\n    //repeat until all change is given\n  function cashReturn (cashToGive){\n}\n</code></pre>\n<p>It wasn’t until right before I went to bed after the first day of tackling the bonfire that I had the epiphany I needed. Previously, I had been trying to figure out some way to glean the information I needed from the difference between the cash in a particular drawer (say the hundreds drawer) and the total amount of change owed. But that was silly. What I really needed to be doing was subtracting multiples of the appropriate denomination. My pre-bed idea looked like this:</p>\n<pre><code class=\"language-javascript\">var hundreds = 0;\nif (cashToGive > 100 &#x26;&#x26; cid[8][1] !== 0 {\n   cashToGive -= 100;\n   hundreds += 1;\n   if (cid[8][1] > hundreds *100){\n      cid[8][1] = hundreds *100;\n   } else {\n      return cid[8][1];\n   }\n  return cid[8];\n}\n</code></pre>\n<p>That meant updated the cashToGive by subtracting the appropriate denomination every time; that would allow me to keep running through the function until I had paid the correct amount. My change would then report out how dollars by multiplying the number of hundreds I took out times a hundred.</p>\n<h2>Close but No Cigar</h2>\n<p>I was close. Really close. But naturally, there were some kinks to work out. For one thing, my original thoughts don’t account for the actual amount of money in the drawer. For example, if I owed <span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mn>2</mn><mn>0</mn><mn>0</mn><mi>d</mi><mi>o</mi><mi>l</mi><mi>l</mi><mi>a</mi><mi>r</mi><mi>s</mi><mi>b</mi><mi>u</mi><mi>t</mi><mi>I</mi><mi>o</mi><mi>n</mi><mi>l</mi><mi>y</mi><mi>h</mi><mi>a</mi><mi>d</mi></mrow><annotation encoding=\"application/x-tex\">200 dollars but I only had</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.69444em;\"></span><span class=\"strut bottom\" style=\"height:0.8888799999999999em;vertical-align:-0.19444em;\"></span><span class=\"base\"><span class=\"mord\">2</span><span class=\"mord\">0</span><span class=\"mord\">0</span><span class=\"mord mathit\">d</span><span class=\"mord mathit\">o</span><span class=\"mord mathit\" style=\"margin-right:0.01968em;\">l</span><span class=\"mord mathit\" style=\"margin-right:0.01968em;\">l</span><span class=\"mord mathit\">a</span><span class=\"mord mathit\" style=\"margin-right:0.02778em;\">r</span><span class=\"mord mathit\">s</span><span class=\"mord mathit\">b</span><span class=\"mord mathit\">u</span><span class=\"mord mathit\">t</span><span class=\"mord mathit\" style=\"margin-right:0.07847em;\">I</span><span class=\"mord mathit\">o</span><span class=\"mord mathit\">n</span><span class=\"mord mathit\" style=\"margin-right:0.01968em;\">l</span><span class=\"mord mathit\" style=\"margin-right:0.03588em;\">y</span><span class=\"mord mathit\">h</span><span class=\"mord mathit\">a</span><span class=\"mord mathit\">d</span></span></span></span>100 dollars in the hundreds drawer, my solution wouldn’t deal with that.</p>\n<h3>A String where Your Number Should Be</h3>\n<p>Strings also became an issue early on. I updated my original changeOwed and totalCash to cut off at 2 decimal places using the toFixed method, to avoid the problem with absurd amounts of decimal places. However, I didn’t know that toFixed returned a string. That became an issue with one of the test cases where, even though there was enough cash in the drawer (totalCash) the program was returning “Insufficient Funds” because, based on unicode values, the totalCash string was less than the changeOwed string. I fixed it by using parseFloat:</p>\n<pre><code class=\"language-javascript\">// find change owed\n  var changeOwed = cash\"price;\",\n  changeOwed = parseFloat(changeOwed.toFixed(2)); //rounds to two decimal places\n  // find total cash in drawer\n  var totalCash = [];\n  totalCash = totalCash.concat.apply(totalCash, cid); //projects and flattens cid array\n  totalCash = totalCash.filter(function(cashItem){\n    if (typeof cashItem === \"number\"){\n      return cashItem;\n    }\n  }).reduce(function(prev, next){\n    return prev + next;\n  });\n  totalCash = parseFloat(totalCash.toFixed(2));//rounds to two decimal places\n</code></pre>\n<p>Similarly, I experienced an issue where reporting out pennies weren’t working because of the decimal places that were turning up every time I subtracted from cashToGive, so I fixed that, as well:</p>\n<pre><code class=\"language-javascript\">function cashReturn (cashToGive){\n     cashToGive = parseFloat(cashToGive.toFixed(2)); //Fixes decimal issue with pennies\n</code></pre>\n<h3>Push It</h3>\n<p>After implementing recursion properly, the biggest issue then became that my solution was only reporting out the last drawer, not pushing all of the necessary drawers. So for instance, if I owed $96.74, my function was only returning [[‘PENNIES’, 0.04]]. Why? Well, originally, I ended my main if/else if/ else statement by pushing the results of my function into the change array. The problem with that was, that even though the function was going through the motions properly, it was only returning the very last action. After much trial and error, I refactored my solution so as to simply call the cashReturn function and handled all the pushing to the change array in the cashReturn function:</p>\n<pre><code class=\"language-javascript\">//if changeOwed > totalCash\n    //return the string \"Insufficient Funds\"\n   if (changeOwed > totalCash){\n     change = \"Insufficient Funds\";\n    } else if (changeOwed === totalCash){\n      //else if changeOwed === totalCash\n    //return the string \"Closed\"\n      change = \"Closed\";\n    } else {\n      //Else return array of change denominations, starting from the highest possible denom to the lowest\n      //implies recursion?\n      change = [];\n      cashReturn(changeOwed);\n    }\n</code></pre>\n<p>But that STILL didn’t work. Why? Well, the issue there was that, originally, there was nothing in the function telling it to push out the results of a particular drawer, before moving on the next. In other words, the function might determine that I needed to take 15 dollars out of the fives drawer before moving on to the ones drawer but it never pushed the results of the fives drawer. So I fixed that by refactoring the if/else statements in each drawer block:</p>\n<pre><code class=\"language-javascript\">if (cid[8][1] !== 0 &#x26;&#x26; cashToGive >= 100){\n        cashToGive -= 100;\n        hundreds += 1;\n        cid[8][1] = hundreds * 100;\n        if (cashToGive !== 0 &#x26;&#x26; cashToGive &#x3C; 100 ){\n          change.push(cid[8]);\n          return cashReturn(cashToGive);\n        } else {\n          return cashReturn(cashToGive);\n        }\n</code></pre>\n<p>As you can see, if cashToGive is not equal to 0 and it drops below the particular denomination, then I push the results of the drawer and keep running it through the cashReturn funtion. Otherwise, I just run it through the return function again.</p>\n<h3>More Money Than It’s Worth</h3>\n<p>Last but not least, I had to tackle the issue of not knowing how much was in the original drawer. As I said earlier, I need to be able to stop taking twenties out of the twenties drawer, for example, once I’d used them all. To tackle that, I made a whole new series of variables to hold the original contents of each drawer (not sure if that was the best way) and then compared those contents to what I wanted to take out, like this:</p>\n<pre><code class=\"language-javascript\">var originalHundreds = cid[8][1],\n      originalTwenties = cid[7][1],\n      originalTens = cid[6][1],\n      originalFives = cid[5][1],\n      originalOnes = cid[4][1],\n      originalQuarters = cid[3][1],\n      originalDimes = cid[2][1],\n      originalNickels = cid[1][1],\n      originalPennies = cid[0][1];\n\n      function cashReturn (cashToGive){\n        [...]\n          else if (cid[7][1] !== 0 &#x26;&#x26; cashToGive >= 20 &#x26;&#x26; twenties * 20 &#x3C; originalTwenties){\n          cashToGive -= 20;\n          twenties += 1;\n          cid[7][1] = twenties * 20;\n          if (cashToGive !== 0 &#x26;&#x26; cashToGive &#x3C; 20 || twenties * 20 === originalTwenties){\n            change.push(cid[7]);\n            return cashReturn(cashToGive);\n          } else {\n            return cashReturn(cashToGive);\n          }\n      }\n  [...]\n</code></pre>\n<h2>Phew!</h2>\n<p>It took two days of work (probably 8 to 10 hours) but I’m really proud of myself for trying to solve this functionally. Some of the other solutions I saw simply got through the test cases. I tried (tried!) to think about other situations to make a better solution. You can find my final solution here: <a href=\"http://www.freecodecamp.com/anthkris\">http://www.freecodecamp.com/anthkris </a></p>\n<p>Feedback welcome!</p>","frontmatter":{"date":"August 06, 2015","path":"/blog/look-ma-im-learnin-freecodecamp-exact-change-bonfire","title":"Look Ma I'm Learnin': FreeCodeCamp Exact Change Bonfire","tags":["coding","FreeCodeCamp","functional programming","JavaScript","recursion"],"categories":["Coding","Showing my Work"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/Exact-Change-Bonfire-8deda12ac76aa5aa1d32268665b9cdc0-81d39.png","srcSet":"/static/Exact-Change-Bonfire-8deda12ac76aa5aa1d32268665b9cdc0-9a8d1.png 203w,\n/static/Exact-Change-Bonfire-8deda12ac76aa5aa1d32268665b9cdc0-c53f9.png 405w,\n/static/Exact-Change-Bonfire-8deda12ac76aa5aa1d32268665b9cdc0-81d39.png 810w,\n/static/Exact-Change-Bonfire-8deda12ac76aa5aa1d32268665b9cdc0-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

